@import url(https://fonts.googleapis.com/css?family=Poppins:600|Roboto:400&display=swap);
/*     TEXT     */
html {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: calc(0.5em + 0.7vw);
  background-color: rgba(0, 0, 0, 1); 
}

body {
  margin: 0;
  color: rgba(0, 0, 0, 1);
  background-color: rgba(255, 255, 255, 1);
  box-shadow: inset 0 0 200px rgba(0, 0, 0, 0.1);
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h1 {
  font-family: "Poppins", sans-serif;
  font-weight: 600;
  font-size: 2.75em;
  line-height: 1.25em;
  margin: 0;
}

h2 {
  font-family: "Poppins", sans-serif;
  font-weight: 600;
  font-size: 2em;
  line-height: 1.3em;
  margin: 0;
}

h3 {
  font-family: "Poppins", sans-serif;
  font-weight: 600;
  font-size: 1em;
  line-height: 1.2em;
  letter-spacing: 3px;
  text-transform: uppercase;
  margin: 0;
}

h4 {
  font-family: "Poppins", sans-serif;
  font-weight: 600;
  font-size: 1.2em;
  line-height: 1.5em;
  margin: 0;
}

h5 {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: 1em;
  line-height: 1.5em;
  margin: 0;
}

p {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: 1.2em;
  line-height: 1.5em;
  margin: 0;
}

a {
  color: #9F9F9F;
  text-decoration: none;
  cursor: pointer;
}

a:hover {
  color: #DFDFDF;
  text-decoration: none;
  cursor: pointer;
}

.button {
  font-family: "Poppins", sans-serif;
  font-weight: 600;
  font-size: 1.1em;
  color: #000000;
  background-color: #DFDFDF;
  padding: 1em 2em 1em 2em;
  margin-top: 6em;
  border: none;
  cursor: pointer;
}

.button:hover {
  color: #DFDFDF;
  background-color: #000000;
}




/*     NAVBAR     */
#nav {
  position: fixed;
  font-family: "Poppins", sans-serif;
  font-weight: 600;
  font-size: 0.8em;
  line-height: 2.5em;
  letter-spacing: 3px;
  text-align: center;
  list-style: none;
  margin: 0;
  padding: 0;
  z-index: 100;
}

#nav li a {
  text-decoration: none;
  color: rgba(0, 0, 0, 1);
}

#nav li a:hover {
  color: rgba(0, 0, 0, 0.3);
  cursor: pointer;
}

#nav-top {
  height: 2.5em;
  width: 100vw;
  position: fixed;
  top: 0;
  z-index: 100;
  background-color: rgba(255, 255, 255, 1);
}

#nav-right {
  height: 2.5em;
  width: 100vh;
  position: fixed;
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
  -webkit-transform-origin: left top;
          transform-origin: left top;
  left: 100%;
  z-index: 100;
  background-color: rgba(255, 255, 255, 1);
}

#nav-bottom {
  height: 2.5em;
  width: 100vw;
  position: fixed;
  bottom: 0;
  z-index: 100;
  background-color: rgba(255, 255, 255, 1);
}

#nav-left {
  height: 2.5em;
  width: 100vh;
  position: fixed;
  -webkit-transform: rotate(-90deg);
          transform: rotate(-90deg);
  -webkit-transform-origin: right top;
          transform-origin: right top;
  right: 100%;
  z-index: 100;
  background-color: rgba(255, 255, 255, 1);
}





/*     PROJECTS     */
#title {
  background-color: #DFDFDF;
  padding-top: 2.5em;
  padding-left: 3.5em;
  height: 2.5em;
  font-family: "Poppins", sans-serif;
  font-size: 0.8em;
  line-height: 2.6em;
  z-index: 0;
}

.scrollbar {
  position: relative;   
  padding: 0;   
  margin: 0;
}

#project-page {
  min-height: 100vh;
  padding: 4em 6em 6em 6em;
  box-sizing: border-box;
}

  /* Small Devices, Tablets */
@media only screen and (max-width: 768px) {
  #project-page {
    padding: 2.5em 2.5em 2.5em 2.5em;
  }
}

.project-title {
  position: fixed;  
  width: 100%;
}

.project-caption-half {
  width: 50%;
  display: inline-block;
  vertical-align: top;
  padding-right: 2.5em;
  box-sizing: border-box;
}

.project-caption {
  width: 100%;
}

.project-text-half {
  width: 50%;
  display: inline-block;
  vertical-align: top;
  padding: 0 2.5em 0 2.5em;
  box-sizing: border-box;
}

  /* Small Devices, Tablets */
@media only screen and (max-width: 768px) {
  .project-text-half {
    width: 100%;
  }
}

.project-text {
  width: 80%;
  padding: 0 2.5em 0 2.5em;
}

img {
  padding: 0 2.5em 0 2.5em;
  width: 100%;
  height: auto;
  box-sizing: border-box;
}

video {
  padding: 0 2.5em 0 2.5em;
  width: 100%;
  height: auto;
  box-sizing: border-box;
}

.padding-top-05em {
  padding-top: 0.5em;
}

.padding-top-1em {
  padding-top: 1em;
}

.padding-top-2em {
  padding-top: 2em;
}

.padding-top-3em {
  padding-top: 3em;
}





/*     BLOG     */
.blog-list-image {
  padding: 0;
  width: 25%;
  vertical-align: top;
}

.blog-list {
  width: 75%;
  padding: 0 2.5em 0 2.5em;
  display: inline-block;
  vertical-align: middle;
  box-sizing: border-box;
}









